<template>
  <div id="app">
    <div id="head">
      <header>
        <img src="./assets/header.png" alt="Vue.js PWA" class="header-bar">
      </header>
    </div>
    <main>
      <div style="padding-top:64px;">
        <router-link to="/Home">Home</router-link>
        <router-link to="/About">About</router-link>
      </div>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
import { storage } from './utils/storage'

export default {
  name: 'app',
  created: function () {
    if (storage.valueExists('last')) {
      let dest = storage.getValue('last')
      this.$router.push(dest)
    }
  },
  watch: {
    '$route' (to, from) {
      if (to.name === null) {
        storage.storeValue('last', '/')
      } else {
        storage.storeValue('last', to.name)
      }
    }
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

img {
  width: 100%;
}

main {
  text-align: center;
}

#head { 
  border-bottom:solid 1px #dedede;
  width:100%;
  z-index:10000;
  background-color:#efefef;
  position: fixed;
}

.header-bar { 
  padding-top: 10px;
  height: 36px;
  width: 100%;
  max-height: 36px;
}
header { 
  width:100%;
  margin: 0;
  height: 56px;
  color: #999;

  padding: 0px auto;
  text-align: center;
}

</style>



