<template>
  <div class="item-out">
    <img v-bind:src="item.logo.url" style="border-radius: 15px; max-width:480px;" v-on:click="showEvent(item.id)">
  </div>
</template>

<script>
export default {
  name: 'eventitem',
  props: [
    'item'
  ],
  methods: {
    showEvent (id) {
      this.$router.push(`/event/${id}`)
    }
  }
}
</script>

<style>
  .item-out {
    width: inherit;
    padding: 2%;
    text-align: left;
  }
</style>



