const state = {
  appState: 'somewhere'
}

const mutations = {
  setAppState (state, loc) {
    state.appState = loc
  }
}

const getters = {
  appState (state) {
    return state.appState
  }
}

const actions = {

}

export default {
  state,
  actions,
  mutations,
  getters
}



