import { storage } from '../../utils/storage'

const state = {
  allData: null
}

const mutations = {
  setData (state, data) {
    state.allData = data
  }
}

const getters = {
  appData (state) {
    return state.allData
  },
  getEventById: (state) => (id) => {
    let item, t
    for (let i = 0; i < state.allData.data.length; i++) {
      t = state.allData.data[i]
      if (t.id === id) {
        item = t
      }
    }
    return item
  }
}

const actions = {
  setData (context, data) {
    storage.storeValue('events', data)
    context.commit('setData', {state, data})
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



