<template>
  <div class="hello">
    MAIN
    <div v-if="events">
      <div v-for="item in events" v-bind:key="item._id">
        <event-item v-bind:item="item"></event-item>
      </div>
    </div>
  </div>
</template>
<script>
import { api } from '../utils/api'
import EventItem from '@/components/element/EventItem'
import { mapActions, mapGetters } from 'vuex'
// import { storage } from '../utils/storage'

export default {
  name: 'hello',
  data () {
    return {
      hasEvents: false,
      events: []
    }
  },
  components: {
    'event-item': EventItem
  },
  mounted: function () {
    api.getRaw('/v1/eb/events/austin').then(
      response => {
        this.events = response.data
        if (this.events.length > 0) {
          this.hasEvents = true
          this.$store.dispatch('setData', this.events)
        }
      })
      .catch(error => {
        console.log(error)
      })
  },
  computed: {
    ...mapGetters([
      'appData'
    ]),
    ...mapActions([
      'setData'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



