<template>
  <div v-if="item" style="margin-bottom:48px;">
    <div style="width:100%; margin-top:48px; text-align:center;">
      <img :src=item.logo.url style="width:100%; max-width:480px;"/>
    </div>
    <p><br/>{{item.name.html}}</p>
    <span v-html=item.description.html></span>
    <button @click="openlink()">MORE INFORMATION</button>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'itemview',
  data () {
    return {
      id: null,
      item: null
    }
  },
  mounted: function () {
    this.id = this.$route.params.id
    this.item = this.getEventById(this.id)
  },
  methods: {
    openlink: function (target) {
      window.open(this.item.url, '_blank')
    }
  },
  computed: {
    ...mapGetters([
      'getEventById'
    ])
  }
}
</script>

<style>

</style>



